export const __metadata = { PACKAGE_NAME: '@wix/stores' };

/** @internal */
export function getAbandonedCart(
  httpClient: HttpClient
): GetAbandonedCartSignature {
  return (_id: string) =>
    universalGetAbandonedCart(
      _id,
      // @ts-ignore
      { httpClient }
    );
}

interface GetAbandonedCartSignature {
  /**
   * Returns abandoned carts based on the cart ID
   * @param - Cart ID
   * @returns Cart details
   * @deprecated
   */
  (_id: string): Promise<AbandonedCart>;
}

/** @internal */
export function queryAbandonedCarts(
  httpClient: HttpClient
): QueryAbandonedCartsSignature {
  return (options?: QueryAbandonedCartsOptions) =>
    universalQueryAbandonedCarts(
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface QueryAbandonedCartsSignature {
  /**
   * Returns abandoned carts based on the requested query parameters
   * @deprecated
   */
  (
    options?: QueryAbandonedCartsOptions | undefined
  ): Promise<QueryAbandonedCartsResponse>;
}

export const onAbandonedCartCartAbandonedEvent = EventDefinition(
  'com.wix.ecommerce.cart.abandonment.api.v1.CartAbandonedEvent',
  false,
  (event: AbandonedCartCartAbandonedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [{ path: 'data.creationTime' }, { path: 'data.abandonTime' }],
        },
        {
          transformFn: transformRESTFloatToSDKFloat,
          paths: [
            { path: 'data.totals.subtotal' },
            { path: 'data.totals.total' },
          ],
        },
      ])
    )
)<AbandonedCartCartAbandonedEnvelope>();
export const onAbandonedCartCartRecoveredEvent = EventDefinition(
  'com.wix.ecommerce.cart.abandonment.api.v1.CartRecoveredEvent',
  false,
  (event: AbandonedCartCartRecoveredEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'data.recoveredTime' },
            { path: 'data.creationTime' },
            { path: 'data.abandonedTime' },
          ],
        },
      ])
    )
)<AbandonedCartCartRecoveredEnvelope>();

